import {useEffect, lazy, Suspense, useState, useContext} from 'react';
import { Route, Switch, useRouteMatch, useHistory, Redirect, Link } from 'react-router-dom';
import Loading from '../components/Loading';

import image from './image/gamysh.jpg';
import image1 from './image/hotel.jpg'
import image2 from './image/hotel1.jpg'
import image3 from './image/hotel2.jpeg'
import { Icon } from 'react-icons-kit'
import {smallDown} from 'react-icons-kit/entypo/smallDown';
import {heartO} from 'react-icons-kit/fa/heartO'
import {mapMarker} from 'react-icons-kit/fa/mapMarker'
import {ic_room} from 'react-icons-kit/md/ic_room'
import {ic_check} from 'react-icons-kit/md/ic_check'
import {ic_close} from 'react-icons-kit/md/ic_close'
import {ic_swap_vert} from 'react-icons-kit/md/ic_swap_vert'
import {map} from 'react-icons-kit/fa/map'
import {ic_tune} from 'react-icons-kit/md/ic_tune'
import {ic_delete} from 'react-icons-kit/md/ic_delete'
import {useDispatch, useSelector} from 'react-redux';
import { post, fetchData } from '../application/middlewares/index';
import { getCategory,getFilters, getToken } from '../application/selectors/auth';
import Flickity from 'react-flickity-component';
import toast from 'react-hot-toast';
import No_goods from './image/no_wishlist.svg';
import Lang1 from '../pages/Language/Lang1';
import { LangContext } from "../pages/Language/LangContext";
import logo1 from './image/logo_gray.png'



const Navbar = lazy(() => import('./Navbar'))
const Footer = lazy(() => import('./Footer'))

function Wishlist(){
    const flickityOptions = {
        // lazyLoad: true,
        pageDots: true,
        wrapAround: true,
        prevNextButtons:true,
        pageDots:true
    }
  const { lang, dispatchLang } = useContext(LangContext);

    const [sort, setSort] = useState("false");
    const ToggleSort = () => {
        setSort(!sort); 
       };
       const [filter, setFilter] = useState("false");
    const ToggleFilter = () => {
        setFilter(!filter); 
       };
    const [products, setProducts] = useState();
    const [count, setCount] = useState(0)
    const [wish_id, setWish_id] = useState();
    const [allRemove, setAllRemove] = useState();
    const [loading, setLoading] = useState(true);

    const dispatch = useDispatch();
    const history = useHistory();
    const token = useSelector(getToken);
    let dil;
    if (lang=='ru') 
    {dil='ru'} 
    else 
    {dil='tm'}
    // console.log(token)
       useEffect(()=>{
           if(token){
        dispatch(fetchData({
            url:`user/${dil}/get-wish-list`,
            token:token,
            action:(response)=>{
                if(response.success){
                    // console.log("get_wishlist",response)
                    // toast.success("Halanlaryma goşuldy!");
                    // console.log(response.data.rows);
                    setProducts(response.data.rows.real_estates_all);
                    setCount(response.data.rows.count);
                    setLoading(false)
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
        }
    },[dil])
    useEffect(()=>{
        if(token && wish_id){
     dispatch(post({
         url:`user/tm/remove-from-wish-list/${wish_id}`,
         token:token,
         action:(response)=>{
             if(response.success){
                //  console.log("get_wishlist",response)
                 toast.error(Lang1[lang].add_errors);
                 // console.log(response.data.rows);
                 setProducts(prev => prev.filter(item => item.id !== wish_id));
                 setCount((count-1))
             } else {
                 console.log("error_data ",response);
                 }
             }
         }))
     }
 },[wish_id])
    
    useEffect(()=>{
        if(token && allRemove){
     dispatch(post({
         url:`user/tm/drop-wish-list`,
         token:token,
         action:(response)=>{
             if(response.success){
                //  console.log("get_wishlist",response)
                 // toast.success("Halanlaryma goşuldy!");
                 // console.log(response.data.rows);
                 setProducts([]);
                 setCount(0)
             } else {
                 console.log("error_data ",response);
                 }
             }
         }))
     }
 },[allRemove])
 

    return(
        <main>
            <Navbar />
            {count==0 ?
            <div className="flex flex-col items-center justify-center w-full h-screen">
                <img src={No_goods} className="w-40 h-40"/>
                <h1 className="text-lg sm:text-xl text-yellow-600 uppercase font-medium mt-5 ml-8">{Lang1[lang].wish_no}</h1>
            </div>
            :
              loading
            ?
            <div className='flex items-center justify-center w-full h-screen'><Loading type="table"/></div>
            :
            <div>
            <div className="main py-4 sm:py-8 border-t">
                <div className="main1">
                <h1 className="text-xl sm:text-3xl font-medium mb-4">{Lang1[lang].wished}</h1>
                <div className="flex justify-between">
                    <div className="flex items-center">
                    <p className="font-medium text-sm sm:text-base">{Lang1[lang].wished_count} {count}</p>
                    {/* <p onClick={ToggleSort} className="relative px-3 py-1 mx-4 rounded-md  font-medium cursor-pointer text-blue-600 hover:bg-gray-200 active:border-1 active:border-blue-600">
                        <Icon size={22} icon={ic_swap_vert} className=" text-blue-600 mr-2"/>По дате добавления в избранное
                        <div className={sort ? "hidden" : "dropdown_filter2 border shadow-xl text-sm rounded-md"}>
                                <p className="flex justify-between p-2 hover:bg-gray-100">
                                    <p>Lorem kj d b kjd kjn ajsd ks a d nka jnd kdsnaskd </p>
                                    <Icon size={18} icon={ic_check} className="text-blue-600" />
                                </p>
                                <p className="flex justify-between  p-2 hover:bg-gray-100">
                                    <p>Lorem</p>
                                </p>
                                <p className="flex justify-between  p-2 hover:bg-gray-100">
                                    <p>Lorem</p>
                                    <Icon size={18} icon={ic_check} className="text-blue-600" />
                                </p>
                                <p className="flex justify-between  p-2 hover:bg-gray-100">
                                    <p>Lorem</p>
                                </p>
                                <p className="flex justify-between  p-2 hover:bg-gray-100">
                                    <p>Lorem</p>
                                </p>
                                <p className="flex justify-between  p-2 hover:bg-gray-100">
                                    <p>Lorem</p>
                                </p>
                        </div>
                        </p> */}
                        {/* <p onClick={ToggleFilter} className="relative px-3 py-1 mx-4 rounded-md  font-medium cursor-pointer text-blue-600 hover:bg-gray-200 active:border-1 active:border-blue-600">
                        <Icon size={22} icon={ic_tune} className=" text-blue-600 mr-2"/>Фильтр
                        <div className={filter ? "hidden" : "dropdown_filter2 border shadow-xl text-sm rounded-md"}>
                                <p className="flex justify-between  p-2 hover:bg-gray-100">
                                    <p>Lorem</p>
                                    <Icon size={18} icon={ic_check} className="text-blue-600" />
                                </p>
                                <p className="flex justify-between  p-2 hover:bg-gray-100">
                                    <p>Lorem</p>
                                </p>
                                <p className="flex justify-between  p-2 hover:bg-gray-100">
                                    <p>Lorem</p>
                                </p>
                        </div>
                        </p> */}
                    </div>
                    <p onClick={()=>{setAllRemove(1);}}
                    className="px-3 py-1 rounded-md text-sm sm:text-base flex items-center font-medium cursor-pointer text-red-600 hover:bg-red-100 "> 
                    <Icon size={18} icon={ic_delete} className="text-red-600 mr-2 " />
                    <span>{Lang1[lang].delete}</span></p>
                </div>
                </div>
            </div>
            <div className="main py-4 sm:py-8">
                <div className="main1">
                    <div className="products w-full flex flex-col">
                        {products?.map((item)=>(
                            <div className="w-full">
                                <div className="product_box1 flex flex-col sm:flex-row justify-between w-full rounded-md p-0 sm:p-5">
                                    <div className="time1 text-gray-500 text-sm">{item?.created_at}</div>
                                    <div className="slider1">
                                    {item?.images == null 
                                    ?
                                    <div className='imgg flex justify-center items-center bg-gray-200'>
                                    <img src={logo1} className="img1" />
                                    </div>
                                    :
                                    <Flickity
                                className={'carousel1 carousel-main'} // default ''
                                elementType={'div'} // default 'div'
                                options={flickityOptions} // takes flickity options {}
                                disableImagesLoaded={false} // default false
                                reloadOnUpdate // default false
                                static // default false
                                >
                                    {item?.images?.map((img)=>(
                                        <div className="carousel1-cell">
                                            <img src={`https://gamysh.com/${img?.destination}-big.webp`} className="img" />
                                        </div>
                                    ))}
                                </Flickity>
                                }
                                        {/* <img src={image2} className="img"/> */}
                                    </div>
                                    <div  className="product_text sm:mb-0 mb-5 flex flex-col items-between p-2">
                                        <div className='w-full'>
                                        <h1 onClick={()=> history.push(`/single/${item.id}`)} className="cursor-pointer text-lg sm:text-2xl text-blue-600 font-medium ">{item?.real_estate_name}</h1>
                                        <p onClick={()=> history.push(`/single/${item.id}`)} className="cursor-pointer text-sm text-gray-500 my-1 sm:my-3">{item?.location}</p>
                                        <h1 onClick={()=> history.push(`/single/${item.id}`)} className=" cursor-pointer text-base sm:text-xl font-medium mb-2 sm:mb-5">
                                            {/* {item?.price} */}
                             {item?.price.slice(0, item?.price.length%3)}{item?.price.length%3 ? ` ` : ``}{(item?.price.slice(item?.price.length%3, item?.price.length).match(/.{1,3}/g).join(' '))} 
                                            
                                            </h1>
                                        </div>
                                        <p onClick={()=> history.push(`/single/${item.id}`)} className="text-sm text-gray-500 w-full break-words">{item?.description}</p>
                                        <div className="flex mt-3 w-full">
                                        <button onClick={()=> history.push(`/single/${item.id}`)} className="btn1 font-medium text-sm w-36">+993{item?.phone}</button>
                                        {/* <div className="heart2 mx-6 text-gray-600 border rounded-md text-sm font-medium cursor-pointer hover:bg-gray-100"><Icon size={18} icon={heartO}/></div> */}
                                        <div onClick={()=>{
                                            setWish_id(item.id);
                                        }} 
                                        className="location2 text-red-600 border rounded-md text-sm font-medium cursor-pointer hover:bg-red-200"><Icon size={20} icon={ic_delete}/></div>
                                        </div>
                                    </div>
                                </div>
                                <hr />
                            </div>
                        ))}
                        
                        
                    </div>
                </div>
            </div>
            </div>
            
            }
        <Footer />
        </main>

    )
}

export default Wishlist;